
/**
 * 防止重复点击 v-preventClick
 * @module utils/directives/preventClick
 * @author risheng.huang
 * v-preventClick="时间(毫秒数)" 默认2000(2s) 
 * <el-button type="primary" @click="submitForm('ruleForm')" v-preventClick size="mini">确 定</el-button>
 * 
 */

export default {
    install(Vue) {
        Vue.directive('preventClick', {
            inserted(el, binding) {
                el.addEventListener('click', () => {
                    if (!el.disabled) {
                        el.disabled = true
                        setTimeout(() => {
                            el.disabled = false
                        }, binding.value || 2000)
                    }
                })
            }
        })
    }
}